import { useSigner } from '@cetus/hooks'
import { useAccountStore } from '@cetus/stores'
import useNotifiStore from '@cetus/stores/src/notifi'
import { instantiateFrontendClient, UserState } from '@notifi-network/notifi-frontend-client'
import useGetNotifiTargetGroups from './useGetNotifiTargetGroups'
import useNotifiAlerts from './useNotifiAlerts'

export default function useIntNotifi() {
  const tenantId = 'cetus' // dappAddress
  const env = 'Production' // or 'Development'
  const walletBlockchain = 'SUI' // e.g. 'Ethereum'
  const {
    setNotifiClient,
    notifiClient,
    setNotifiStatus,
    setNotifiAccountAddress,
    setDefaultTargetGroup,
    setNotifiSourceGroups,
    setNotifiSources,
    setNotifiLoginLoading,
    setTelegramTarget,
    setEmailTarget,
    setSmsTarget
  } = useNotifiStore()
  const { signUserMessage } = useSigner()
  const { currentAccount } = useAccountStore()
  const { ensureTargetGroup, getNotifiTargetGroups } = useGetNotifiTargetGroups()
  const { getNotifiAlerts } = useNotifiAlerts()
  // åˆå§‹åŒ–notifi
  const initNotifi = async (walletPublicKey: string) => {
    const client = instantiateFrontendClient(
      tenantId,
      {
        walletBlockchain,
        walletPublicKey,
        accountAddress: walletPublicKey
      },
      env
    )
    const initResult: UserState = await client.initialize()
    setNotifiClient(client)
    setNotifiStatus(initResult.status)
    // åˆ·æ–°æˆ–é‡æ–°æ‰“å¼€é¡µé¢notifi ç™»å½•çŠ¶æ€è¿˜åœ¨
    if (initResult?.status === 'authenticated') {
      getNotifiTargetGroups(client)
      getNotifiAlerts(client)
      setNotifiAccountAddress(walletPublicKey)
    } else {
      clearNotifiInfo()
    }
  }

  const notifiLogin = async (targetGroupParams: { emailAddress: string; phoneNumber: string; telegramId: string }) => {
    try {
      setNotifiLoginLoading(true)
      console.log('ðŸš€ðŸš€ðŸš€ ~ useInitNotifi.ts:63 ~ notifiLogin ~ notifiClient:', notifiClient)
      const loginResult = await notifiClient.logIn({
        walletBlockchain,
        signMessage: async (message: Uint8Array) => {
          console.log('ðŸš€ðŸš€ðŸš€ ~ useInitNotifi.ts:63 ~ signMessage: ~ message:', message)
          try {
            const signature = await signUserMessage(message)
            return signature.signature
          } catch (error) {
            console.log('ðŸš€ðŸš€ðŸš€ ~ useInitNotifi.ts:67 ~ signMessage: ~ error:', error)
          }
        }
      })
      console.log('ðŸš€ðŸš€ðŸš€ ~ useInitNotifi.ts:67 ~ notifiLogin ~ notifiClient:', notifiClient)
      const { status } = notifiClient.userState
      setNotifiStatus(status)
      if (status === 'authenticated' && currentAccount?.address) {
        getNotifiAlerts()
        getNotifiTargetGroups()
        setNotifiAccountAddress(currentAccount.address)
      }
      console.log('ðŸš€ðŸš€ðŸš€ ~ useInitNotifi.ts:75 ~ notifiLogin ~ loginResult:', loginResult)
      if (loginResult) {
        // ç™»å½•æ—¶é‡æ–°è®¾ç½®TargetGroup
        await ensureTargetGroup(targetGroupParams)
        setNotifiLoginLoading(false)
      }
    } catch (error) {
      console.log('ðŸš€ðŸš€ðŸš€ ~ useInitNotifi.ts:91 ~ notifiLogin ~ error:', error)
      setNotifiLoginLoading(false)
    }
  }

  const clearNotifiInfo = () => {
    setDefaultTargetGroup([])
    setNotifiSourceGroups([])
    setNotifiSources([])
    setEmailTarget({})
    setTelegramTarget({})
    setSmsTarget({})
  }
  return { initNotifi, notifiLogin }
}
